.reviews__list{
  margin-bottom: 10px;
}
.reviews__item{
  border: 1px solid $color-silver;
  padding: 30px;
  &-title{
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
  }
  &-descr{
    color: #999;
  }
  &-text:not(:last-child){
    margin-bottom: 10px;
  }
  &:not(:last-child){
    margin-bottom: 40px;
  }
}
.reviews__info{
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  svg{
    margin-right: 30px;
    width: 100%;
    height: 70px;
    max-width: 70px;
    fill: $color-main;
  }
}
